import './main.css'
import './logs.less'
import './scss.scss'
import * as $ from 'jquery'


$(function() {

    // Functions
    const clrExp = ()=> $('.expression').val("")
    const getExp = ()=> $('.expression').val()
    const setExp = (res)=> $('.expression').val(res)
    const addExp = (add)=> $('.expression').val(getExp()+add)
    const getBtn = (th)=> $(th).attr('a')
    
    function loadLogs(){
        $.ajax({ url: "/api/calc/logs"}).done(function(data) {               
            let o = ""
            for(let v of data) o +="Expression: "+v.expression+", Result: <strong>"+v.result+"</strong><br>"
            $('.logs').html(o)
        });   
    }

    function sendExp(exp){
        $.ajax({ url: "/api/calc/logs", type : 'PUT', data :{'data': exp} })
            .fail(function(xhr){
                $('.errors').html(xhr.responseJSON['data'])
            })
            .done(function(data) {                  
                setExp(data.result)
                $('.errors').html("");
                loadLogs();
        });   
    }

    // Load last logs
    loadLogs();

    // Events
    $(document).on('keypress', function(e) {
        if(e.which == 13) {
            sendExp(getExp());
        }
    });

    $(document).on("click",".btn", function() {     
        let btnVal = getBtn(this);
        if(btnVal==="="){
            sendExp(getExp());          
        }else if(btnVal==="Clear"){
            clrExp();
        }else{
            addExp(btnVal);
        }
    });

});